@charset "utf-8";
// 导航菜单
.navmenu {
    border: 0px;
    width: 100%;
    height: 36px;
}

.Nav {
    width: 1200px;
    margin: auto;
    font-family: "Verdana", "Arial", Microsoft Yahei, Arial;
    font-weight: bold;
    height: 36px;
    line-height: 36px;
}


/* 左侧二级菜单 */

.Nav_l {
    z-index: 2;
    background: #cc0000;
    width: 174px;
    float: left;
    position: relative;
}


/* 导航条 */

.menu_mid {
    width: 990px;
    overflow: hidden;
    margin-left: 196px;
    ul {
        float: left;
        li {
            text-align: center;
            float: left;
            +li {
                margin-left: 30px;
            }
            a {
                float: left;
                height: 36px;
                line-height: 36px;
                font-size: 14px;
                color: #424242;
                font-weight: normal;
            }
        }
    }
}


/* 二级导航标题 */

.Nav_l h2 {
    width: 174px;
    height: 36px;
    line-height: 36px;
    color: #fff;
    font-size: 15px;
    font-weight: bold;
    padding-left: 32px;
    float: left;
    box-sizing: border-box;
    a {
        color: #fff;
        font-size: 12px;
        font-weight: normal;
    }
}


/* 二级导航主盒子 */

.Nav_classify {
    position: absolute;
    top: 36px;
    left: 0;
    width: 174px;
    height: auto;
    border-top: none;
    z-index: 9999;
    background: rgba(0, 0, 0, 0.55);
    ul {
        width: 174px;
        height: 450px;
        overflow: hidden;
    }
    li {
        cursor: pointer;
        min-height: 38px;
        color: #faf8f9;
        position: relative;
        z-index: 9999;
    }
}


/* 第一条样式demo */

.classify_list {
    display: block;
    cursor: pointer;
    line-height: 40px;
    height: 40px;
    color: #faf8f9;
}

.Nav_classify {
    li {
        h3 {
            line-height: 40px;
            height: 40px;
            font-size: 12px;
            font-weight: normal;
            padding-left: 32px;
            overflow: hidden;
        }
        a {
            color: #faf8f9;
        }
        i {
            position: absolute;
            top: 0px;
            right: 10px;
        }
    }
    .classify_list1 {
        width: 990px;
        min-height: 450px;
        box-sizing: border-box;
        overflow: hidden;
        border: 1px solid #d3d3d3;
        background: #fff;
        position: absolute;
        left: 174px;
        top: 0px;
        z-index: 999;
        display: none;
        box-shadow: 6px 3px 8px 0 rgba(195, 183, 183, 0.28);
        .nav_sub1 {
            float: left;
            width: 672px;
            min-height: 450px;
            padding: 28px 80px 28px 0;
            box-sizing: border-box;
        }
    }
}

.Nav_classify>ul>li.actived {
    background-color: greenyellow;
}

// .Nav_classify .classify_list1.hide {
//     display: none;
// }
.Nav_classify .classify_list1.show {
    display: block;
}